$ratio: 750/640;
$browser-default-font-size: 14;
$browser-default-font-size-px: 14px !default;
$lte7:                  true !default; //是否兼容ie6-7

@function r($px, $isFixed:true) {
  @if $px != 0 {
    @return if($isFixed, $px / $ratio + 0.01, $px / $ratio) / $browser-default-font-size * 1rem;
  } 
  @else
  {
    @return 0;
  }
}
$designWidth : 750; //如设计图是750
$deviceWidth : 320;//视图的宽度
@function pxToRem( $px ){
    @return $px * $deviceWidth / $designWidth / $browser-default-font-size * 1rem;; 
}


@font-face {
  font-family: 'iconfont';  /* project id 1032728 */
  src: url('//at.alicdn.com/t/font_1032728_6t4oloela9o.eot');
  src: url('//at.alicdn.com/t/font_1032728_6t4oloela9o.eot?#iefix') format('embedded-opentype'),
  url('//at.alicdn.com/t/font_1032728_6t4oloela9o.woff2') format('woff2'),
  url('//at.alicdn.com/t/font_1032728_6t4oloela9o.woff') format('woff'),
  url('//at.alicdn.com/t/font_1032728_6t4oloela9o.ttf') format('truetype'),
  url('//at.alicdn.com/t/font_1032728_6t4oloela9o.svg#iconfont') format('svg');
}

.iconfont {
  font-family: "iconfont" !important;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  speak: none;

  &:before {
    content: attr(data-font);
  }
}